@import url(https://fonts.googleapis.com/css2?family=Nothing+You+Could+Do&display=swap);
html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
  max-width: 100%;
}
body {
  /* min-height: 100%; */
  height: 100vh;
  /* background-color: #0a2b5c; */
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(10, 43, 92, 0.40)), to(rgba(10, 43, 92, 0.40))),
  url(/static/img/background-sgabriel.acaccf2.jpg);
  background: linear-gradient(rgba(10, 43, 92, 0.40), rgba(10, 43, 92, 0.40)),
  url(/static/img/background-sgabriel.acaccf2.jpg); /*background image + tint*/
  background-attachment: fixed;
  background-size: 100% auto;
  background-position: center;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: white;
  padding: 60px 0; /* note to self, using margin here instead makes 
  pages longer and scroll when they dont need to */
  width: 100%;
}
h1 {
  color: white;
  font-family: 'Nothing You Could Do', cursive;
  font-size: 4em;
}
.header-wrapper {
  margin: auto;
  padding: 2em 5%;
}
.header-inside {
  border: 2px solid white;
}
.header-inside h1 {
  margin: .7em 0 .3em 0;
}
.timeperiod-button-container {
  margin-bottom: 2.1em;
}
.timeperiod-button-container button {
  cursor: pointer;
  outline: none;
  background-color: transparent;
  border: none;
}
.timeperiod-button-container button span {
  padding: .3em;
  font-size: 1em;
  color: white;
  font-weight: 700;
}
.timeperiod-button-container button span:hover {
  border-bottom: 1px solid white;
}
.active {
  border-bottom: 1px solid white;
}
.timeperiod-button-container button:focus {
  border-bottom: 1px solid white;
}
*,
*:after,
*:before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
@media screen and (max-width: 480px) {
body {
    /* background-attachment doesn't work on mobile devices */
    background-size: 100% auto;
    background-position: left top;
    background-attachment: scroll;
}
h1 {
    font-size: 2.5em;
}
.fire {
     top: calc(50% - 50px);
}
.timeperiod-button-container button span {
  font-size: 1em;
}
}
/*Router animation properties*/
.fade-enter-active,
.fade-leave-active {
    -webkit-transition-duration: 0.5s;
            transition-duration: 0.5s;
    -webkit-transition-property: opacity;
    transition-property: opacity;
    -webkit-transition-timing-function: ease;
            transition-timing-function: ease;
}
.fade-enter,
.fade-leave-active {
    opacity: 0;
}

html {
  height: 100%;
}
.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: 36px;
  top: 36px;
  cursor: pointer;
}
